.App {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.table {
  width: 546px;
  height: 546px;
  display: flex;
  flex-direction: column;
}

.tableRow {
  display: flex;
  flex-direction: row;
}

.square {
  width: 40px;
  height: 40px;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffdbb1;
}

.selected {
  background-color: #f38910;
}

.available {
  background-color: #f5ab57;
}